import React, { useState } from 'react';
import { useLocation } from 'react-router-dom'
import CodeInterpreter from './../../lib/ConsoleInterpreter';
import Toolbar from '~/components/Toolbar/Toolbar';
import TabLayout from '~/components/TabLayout/TabLayout';
import Code from '~/components/Code/Code';

import OutputTextArea from '~/components/OutputTextArea/OutputTextArea';
import VisualValidator from '~/components/VisualValidator/VisualValidator';
import { generateEasyCode } from './../../code-generators/generateEasyCode';

import './PlayPage.scss';
import Lexer, { GrammarRules } from '~/lib/Lexer';

const validDifficulties = ['easy', 'medium', 'hard'];

const interpreter = new CodeInterpreter();

const testCode = `console.log("Hej")`;

function PlayPage() {

  const text = `Hej {{ "en string 31 som \\"innehåller\\" en substring" 4.325 "bla bla" 20 'single "quote' }} då`;

  const actionLexer = new Lexer([
    GrammarRules.string,
    GrammarRules.number,
    GrammarRules.noWhitespace
  ])

  const lexer = new Lexer([
    { 
      pattern: /\{\{((.|\n)*?)\}\}/, 
      type: 'Action', 
      evaluate: (value: string) => {
        const token = { type: 'Action', content: actionLexer.run(value.substr(2, value.length-4)) };
        return token;
      }
    }
  ]);

  const tokens = lexer.run(text);
  console.log(tokens);

  const [ outputText, setOutputText ] = useState('');
  const [ isCheckingOutput, setIsCheckingOutput ] = useState(false);
  const [ code, setCode ] = useState(() => generateEasyCode());

  const location = useLocation();
  
  const isPractice = location.pathname.includes('practice');
  let difficulty = location.pathname.split('/').pop();
  difficulty = validDifficulties.indexOf(difficulty as string) >= 0 ? difficulty : validDifficulties[1];

  interpreter.setCode(code);

  const onGetNewCode = (e: any) => {
    e.preventDefault();
    setOutputText('');
    setIsCheckingOutput(false);
    setCode(generateEasyCode());
  }

  const onCheck = (e: any) => {
    e.preventDefault();
    setIsCheckingOutput(!isCheckingOutput);
  }

  const getOutputContent = () => {
    return (isCheckingOutput
      ? <VisualValidator output={ outputText } correctOutput={ interpreter.getOutput() } />
      : <OutputTextArea text={outputText} onChange={ e => { setOutputText(e) } } />
    );
  }

  const hasCorrectOutput = outputText == interpreter.getOutput();

  return <div className="PlayPage">
    <Toolbar>
      <div className="PlayPage__PracticeTitle">
        <h3>Practice</h3>
        <h4>(Difficulty: { difficulty })</h4>
      </div>
    </Toolbar>

    <TabLayout 
      tabs={[
        { icon: 'code',    label: 'Code',   content: <Code code={code} /> },
        { icon: 'subject', label: 'Output', content: getOutputContent() }
      ]}
    />

    <Toolbar>
      <a href="#" className="PlayPage__Button --subtle"
        onClick={ onGetNewCode }
      ><i className="material-icons">cached</i><span>Get New Code</span></a>
     
      <a href="#" 
        className={`PlayPage__Button${ isCheckingOutput ? ( hasCorrectOutput ? ' --correct' : ' --incorrect' ) : '' }`} 
        onClick={onCheck}
      >
        <i className="material-icons">{ isCheckingOutput ? 'subject' : 'play_arrow' }</i>
        <span>{ isCheckingOutput ? 'Try Again' : 'Check Output' }</span>
      </a>
    </Toolbar>
  </div>
}

export default PlayPage;