export function randomEscapeVariableFromQuotes(value: string, prefixInQuotes: string = ''): string {
  const r = Math.random();
  return (r < 0.1 
    ? prefixInQuotes ? prefixInQuotes + value : value
    : r < 0.2 
      ? prefixInQuotes + "' + '" + value + "' + '"
      : prefixInQuotes + "' + " + value + " + '"
  );
}

export interface Variable {
  name: string,
  value: any,
  key: string
}

export class VariableDictionary {
  private _variables: Array<Variable> = [];

  public getVariableAt(index: number): Variable {
    return this._variables[index];
  }

  public removeVariableAt(index: number): Variable {
    return this._variables.splice(index, 1)[0];
  }

  public getVariablesCountKeyStartsWith(key: string): number {
    let count = 0;
    for(let i = 0; i < this._variables.length; i++) {
      if(this._variables[i].key.startsWith(key)) {
        count++;
      }
    }
    return count;
  }

  public getRandomVariableByKey(key: string): Variable|null {
    const possibleVariables: Array<Variable> = [];
    for(let i = 0; i < this._variables.length; i++) {
      if(this._variables[i].key === key) {
        possibleVariables.push(this._variables[i]);
      }
    }
    return possibleVariables.length > 0 ?  possibleVariables[Math.floor(possibleVariables.length * Math.random())] : null;
  }

  public get variablesCount(): number {
    return this._variables.length;
  }

  public create(key: string, value: any, preferredName?: string): Variable {
    const variable = { 
      name: this._newUniqueVariableName(preferredName),
      value,
      key
     };

     this._variables.push(variable);

     return variable;
  }

  private _newUniqueVariableName(preferredName?: string): string {
    let name: string|undefined = preferredName;
    while(name === undefined || this._hasVariableWithName(name)) {
      name = newVariableName();
    }
    return name;
  }

  private _hasVariableWithName(name: string): boolean {
    for(let i = 0; i < this._variables.length; i++) {
      if(this._variables[i].name === name) {
        return true;
      }
    }
    return false;
  }
}


const possibleVariableNameLetters = 'abcdefghijklmnopqrstuvwxyz';
const invalidNames = ['i','j','x','y','if','for','do','while','let','var','const'];

function newVariableName(): string {
  let name: string = '';
  const length = 1 + Math.floor(3 * Math.random());
  while(name.length < length) {
    const letter = possibleVariableNameLetters.charAt(Math.floor(possibleVariableNameLetters.length * Math.random()));
    name += Math.random() < 0.25 ? letter.toUpperCase() : letter;
    if(name.length === length && invalidNames.indexOf(name) >= 0) {
      name = '';
    }
  }
  name = (Math.random() < 0.5
    ? name.charAt(0).toLowerCase() + name.substr(1, name.length)
    : name.substr(0, name.length - 1) + name.charAt(name.length - 1).toLowerCase()
  )
  return name;
}