import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import ToggleSwitch from '~/components/ToggleSwitch/ToggleSwitch';

import './IndexPage.scss';
import { getHighscore, HighscoreKey } from '~/helpers/highscore';

const gameModes = [ "Game", "Practice" ];
const difficulties = [ "Easy", "Medium", "Hard" ];

function IndexPage() {

  const [ gameModeIndex, setGameModeIndex ] = useState(1);
  const [ difficultyIndex, setDifficultyIndex ] = useState(0);

  return <div className="IndexPage">
    <h2>Learn To Read</h2>
    <h1>Code</h1>
    
    <ToggleSwitch
      label="Select mode"
      options={ gameModes }
      selectedIndex={ gameModeIndex }
      optionWidth="9rem"
      onSelect={ (e) => { setGameModeIndex(e.index) } }
      disabled={true}
    />

    <ToggleSwitch
      label="Select difficulty"
      options={ difficulties }
      selectedIndex={ difficultyIndex }
      optionWidth="6rem"
      onSelect={ (e) => { setDifficultyIndex(e.index) } }
      disabled={true}
    />

    <div className="IndexPage__HighScore">
      <h3>High Score ({ difficulties[difficultyIndex] })</h3>
      <h4>{ getHighscore( difficulties[difficultyIndex].toLocaleLowerCase() as HighscoreKey ) }</h4>
    </div>

    <Link 
      to={
        `/play${ gameModeIndex === 1 ? '/practice' : '' }/${ difficulties[difficultyIndex].toLowerCase() }`
      } 
      className="IndexPage__StartButton"
    >Start</Link>
  </div>
}

export default IndexPage;