// @ts-ignore
import TextDiff from 'text-diff';

export function TokenizeOutput(output: string, correctOutput: string) {
  
  const tokens: any = [];

  const lines = output.split('\n');
  const correctLines = correctOutput.split('\n');
  const maxLineCount = Math.max(lines.length, correctLines.length);

  const diff = new TextDiff();
  for(let i = 0; i < maxLineCount; i++) {
    tokens.push(diff.main(
      i < lines.length ? lines[i] : '',
      i < correctLines.length ? correctLines[i] : ''
    ));
  }

  return tokens;
}
