import React, { useState } from 'react';

import './TabLayout.scss';
import useWindowSize from '~/hooks/useWindowSize';

interface TabDetails {
  icon: string,
  label: string,
  content: any
}

interface TabLayoutProps {
  tabs: Array<TabDetails>
};

function TabLayout(props: TabLayoutProps) {

  const [activeTabIndex, setActiveTabIndex] = useState(0);
  
  const windowSize = useWindowSize();

  const windowed = windowSize.width > 950;

  return <div className={`TabLayout${ windowed ? ' --windowed' : '' }`}>
    <div className="TabLayout__Toolbar">{props.tabs.map((tab: TabDetails, index: number) => {
      return <a 
        href="#" 
        onClick={(e) => { e.preventDefault(); setActiveTabIndex(index) }}
        key={index} 
        className={`TabLayout__TabButton ${!windowed && index === activeTabIndex ? '--active' : ''}`}
      >{ <><i className="material-icons">{tab.icon}</i><span>{tab.label}</span></> }</a> 
    })}</div>
    
    <div className="TabLayout__Content">{props.tabs.map((tab: TabDetails, index: number) => {
      return <div className={ !windowed && activeTabIndex === index ? '--active' : '' } key={index}>
          { tab.content }
        </div>
    })}</div> 
  </div>
}

export default TabLayout;