import { GrammarRule, GrammarRuleEvaluateFunction } from "./Grammar";
import { Token } from "./Token";

export class Lexer {
  private _grammar: Array<GrammarRule>;
  private _combinedPattern: RegExp;

  public constructor(grammar: Array<GrammarRule>) {
    this._grammar = grammar;

    let str = '(';
    for(let i = 0; i < this._grammar.length; i++) {
      str += (i > 0 ? '|' : '') + this._grammar[i].pattern.source;
      this._grammar[i].__exactPattern = new RegExp(`^${this._grammar[i].pattern.source}$`, 'g');
    }
    this._combinedPattern = new RegExp(str + ')', 'g');
  }

  public run(input: string): Array<Token> {
    const tokens = new Array<Token>();
    const matches = input.matchAll(this._combinedPattern);

    let next = matches.next();
    let cursor = 0, buffer = '';
  
    while(cursor < input.length) {
      if(next.done === false && cursor === next.value.index) {
        for(let i = 0; i < this._grammar.length; i++) {
          if(next.value[0].match(this._grammar[i].__exactPattern as RegExp)) {
            if(this._grammar[i].evaluate) {
              const token = (this._grammar[i].evaluate as GrammarRuleEvaluateFunction)(next.value[0]);
              if(token) {
                tokens.push(token);
              }
            } 
            else {
              tokens.push({ type: this._grammar[i].type, content: next.value[0] });
            }
            break;
          }
        }
        cursor += next.value[0].length;
        next = matches.next();
      }
      else {
        buffer += input.charAt(cursor);
        cursor++;
      }
  
      if(buffer.length > 0 && (cursor >= input.length || (next.done === false && cursor === next.value.index))) {
        tokens.push({ type: -1, content: buffer })
        buffer = '';
      }
    }
  
    return tokens;
  }
}