import { generateSentence } from "~/lib/SentenceGenerator/SentenceGenerator";
import { VariableDictionary, Variable } from "../VariableDictionary";
import { SentenceVariableActions } from "../SentenceVariableActions";
import { generateConsoleLog } from "../snippets";

export function consoleLog02() {
  let code = '';

  const dictionary = new VariableDictionary();
  const variableActions = new SentenceVariableActions(dictionary);
  const config = { customActions: variableActions.actions };

  let log = '';
  const count = Math.random() < 0.6 ? 1 : 1 + Math.floor(Math.random() * 3); 
  for(let i = 0; i < count; i++) {
    log += generateConsoleLog(0, "'" + generateSentence(config).trim() + "'") + ";\n";
  }

  const declaredVariables: Array<Variable> = [];
  while(dictionary.variablesCount > 0) {
    const index = Math.floor(dictionary.variablesCount * Math.random());
    const variable = dictionary.removeVariableAt(index);

    if(Math.random() < 0.25 && declaredVariables.length > 0) {
      const variable2 = declaredVariables[Math.floor(declaredVariables.length * Math.random())];
      variable.value = variable2.name;  
    }

    code += 'let ' + variable.name + ' = ' + variable.value + ";\n";
    declaredVariables.push(variable);
  }

  code += log;
  code = code.replace(/\'\' \+ /g, '');
  code = code.replace(/ \+ \'\'/g, '');

  return code;
}