import React, { useEffect } from 'react';
import Prism from 'prismjs';

import './Code.scss';

interface CodeProps {
  code: string
}

function Code( props: CodeProps ) {
  useEffect(() => {
    Prism.highlightAll();
  }, [ props.code ]);
  return <pre className="Code language-js"><code>{ props.code }</code></pre>
}

export default Code;