import { aPrefix } from "~/lib/SentenceGenerator/SentenceGenerator";
import { getRandomAnimal, getRandomAnimalPlural } from "~/lib/SentenceGenerator/animals";
import { getRandomName } from "~/lib/SentenceGenerator/names";
import { getRandomFood, getRandomFoodPlural } from "~/lib/SentenceGenerator/foods";
import { getRandomAdjective } from "~/lib/SentenceGenerator/adjectives";
import { VariableDictionary, randomEscapeVariableFromQuotes } from "./VariableDictionary";
import { randomInt } from "~/helpers/randomInt";

export class SentenceVariableActions {
  public actions: any;
  
  public _dictionary: VariableDictionary;

  public constructor(dictionary: VariableDictionary) {
    this._dictionary = dictionary;

    this.actions = {
      name: () => {
        if(Math.random() < 0.25) { return getRandomName(); }
        let variable = this._dictionary.getRandomVariableByKey('name');
        if(variable === null || Math.random() < 0.5) {
          variable = this._dictionary.create('name', "'" + getRandomName() + "'", Math.random() < 0.5 ? 'name' : undefined);
        }
        return randomEscapeVariableFromQuotes(variable.name);
      },
      adjective: () => {
        if(Math.random() < 0.25) { return getRandomAdjective(); }
        let variable = this._dictionary.getRandomVariableByKey('adjective');
        if(variable === null || Math.random() < 0.5) {
          variable = this._dictionary.create('adjective', "'" + getRandomAdjective() + "'");
        }
        return randomEscapeVariableFromQuotes(variable.name);
      },
      an_adjective: () => {
        if(Math.random() < 0.25) { return aPrefix(getRandomAdjective()); }
        let variable = this._dictionary.getRandomVariableByKey('an_adjective');
        if(variable === null || Math.random() < 0.5) {
          variable = this._dictionary.create('an_adjective', "'" + getRandomAdjective() + "'");
        }
        return randomEscapeVariableFromQuotes(variable.name, aPrefix(variable.value, true));
      },
      foods: () => {
        if(Math.random() < 0.25) { return getRandomFoodPlural(); }
        let variable = this._dictionary.getRandomVariableByKey('foods');
        if(variable === null || Math.random() < 0.5) {
          variable = this._dictionary.create('foods', "'" + getRandomFoodPlural() + "'", Math.random() < 0.33 ? 'food' : undefined);
        }
        return randomEscapeVariableFromQuotes(variable.name);
      },
      a_food: () => {
        if(Math.random() < 0.25) { return aPrefix(getRandomFood()); }
        let variable = this._dictionary.getRandomVariableByKey('a_food');
        if(variable === null || Math.random() < 0.5) {
          variable = this._dictionary.create('a_food', "'" + getRandomFood() + "'", Math.random() < 0.33 ? 'food' : undefined);
        }
        return randomEscapeVariableFromQuotes(variable.name, aPrefix(variable.value, true));
      },
      animal: () => {
        if(Math.random() < 0.25) { return getRandomAnimal(); }
        let variable = this._dictionary.getRandomVariableByKey('animal');
        if(variable === null || Math.random() < 0.5) {
          variable = this._dictionary.create('animal', "'" + getRandomAnimal() + "'", Math.random() < 0.33 ? 'animal' : undefined);
        }
        return randomEscapeVariableFromQuotes(variable.name);
      },
      animals: () => {
        if(Math.random() < 0.25) { return getRandomAnimalPlural(); }
        let variable = this._dictionary.getRandomVariableByKey('animals');
        if(variable === null || Math.random() < 0.5) {
          variable = this._dictionary.create('animals', "'" + getRandomAnimalPlural() + "'", Math.random() < 0.33 ? 'animal' : undefined);
        }
        return randomEscapeVariableFromQuotes(variable.name);
      },
      an_animal: () => {
        if(Math.random() < 0.25) { return aPrefix(getRandomAnimal()); }
        let variable = this._dictionary.getRandomVariableByKey('an_animal');
        if(variable === null || Math.random() < 0.5) {
          variable = this._dictionary.create('an_animal', "'" + getRandomAnimal() + "'", Math.random() < 0.33 ? 'animal' : undefined);
        }
        return randomEscapeVariableFromQuotes(variable.name, aPrefix(variable.value, true));
      },
      uint: () => {
        let value = randomInt(1, 100);
        if(Math.random() < 0.25) { return Math.random() < 0.5 ? value : "' + " + value + " + '" }
        let variable = this._dictionary.getRandomVariableByKey('uint');
        if(variable === null || Math.random() < 0.5) {
          variable = this._dictionary.create('uint', value);
        }
        return randomEscapeVariableFromQuotes(variable.name);
      },
    };
  }
}