// @ts-ignore
import Interpreter from 'js-interpreter';

class ConsoleInterpreter {

  private _interpreterInitFunction: any;
  private _interpreter: Interpreter = null;
  private _output: string = '';

  public constructor() {
    this._interpreterInitFunction = function(interpreter: any, globalObject: any) {
      const console = interpreter.nativeToPseudo({});
      interpreter.setProperty(globalObject, 'console', console);
      interpreter.setProperty(console, 'log', interpreter.createNativeFunction(this._log.bind(this)));
    };
  }

  public setCode(code: string): void {
    code = code.replace(/let/g, 'var');
    this._interpreter = new Interpreter(code, this._interpreterInitFunction.bind(this));
    this._run();
  }

  public getOutput(): string {
    return this._output;
  }

  private _run(): void {
    this._output = '';
    this._interpreter.run();

    // Remove the last new line from the console log.
    this._output = this._output.substr(0, Math.max(0, this._output.length - 1));
  }

  private _log(message: any): void {
    this._output += message + '\n';
  }
}

export default ConsoleInterpreter;