import { NumberVariableActions } from "./NumberVariableActions";
import { VariableDictionary } from "./VariableDictionary";
import { generateSentence } from "~/lib/SentenceGenerator/SentenceGenerator";

const INDENT_SPACES = '  ';


// console.log
// ========================================

export function generateConsoleLog(indent: number, message: string): string {
  return generateIndent(indent) + 'console.log(' + message + ')';
}


// equations
// ========================================

const easyEquationTemplates = [
  '{{ int100 }} + {{ int100 }}',
  '{{ int100 }} + {{ int100 }}',
  '{{ int100 }} + {{ int100 }}',
  '{{ int100 }} + {{ int100 }}',
  
  '{{ int10 }} + {{ int10 }} + {{ int10 }}',
  '{{ int10 }} + {{ int10 }} + {{ int10 }}',
  '{{ int10 }} + {{ int10 }} + {{ int10 }}',

  '{{ int10 }} + {{ int10 }} + {{ int10 }} + {{ int10 }}',
  '{{ uint100_randomAsString }} + {{ uint100_randomAsString }}',
  '{{ uint10_randomAsString }} + {{ uint10_randomAsString }} + {{ uint10_randomAsString }}',

  '{{ int10 }} * {{ int10 }}',
  '{{ int10 }} * {{ int10 }} + {{ int10 }}',
  '{{ int10 }} * {{ int10 }} + {{ uint10_randomAsString }}',
  '{{ int10 }} * {{ int10 }} - {{ int10 }}',
  '{{ int10 }} + {{ int10 }} * {{ int10 }}',
  '{{ uint10_randomAsString }} + {{ int10 }} * {{ int10 }}',
  '{{ int10 }} - {{ int10 }} * {{ int10 }}',

  '{{ division_equation }}',
  '{{ division_equation }} + {{ int10 }}',
  '{{ division_equation }} + {{ uint10_randomAsString }}',
  '{{ division_equation }} - {{ int10 }}',
  '{{ int10 }} + {{ division_equation }}',
  '{{ uint10_randomAsString }} + {{ division_equation }}',
  '{{ int10 }} - {{ division_equation }}'
];

const numberVariableActions = new NumberVariableActions(null);
const easyEquationsConfig = { templates: easyEquationTemplates, firstCharacterToUpperCase: false, customActions: numberVariableActions.actions };

export function generateEasyEquation(dictionary: VariableDictionary): string {
  numberVariableActions.dictionary = dictionary;
  return generateSentence(easyEquationsConfig);
}


// indent
// ========================================

export function generateIndent(size: number): string {
  let indent = '';
  for(let i = 0; i < size; i++) {
    indent += INDENT_SPACES;
  }
  return indent;
}