export function getRandomTemplate(): string {
  return templates[Math.floor(templates.length * Math.random())];
}

export const templates = [
  '{{ name }} is {{ adjective }}',
  'The {{ animal }} is {{ adjective }}',

  '{{ adjective }} {{ name }}',
  '{{ an_adjective }} {{ animal }}',

  '{{ name }} loves {{ animals }}',
  '{{ name }} hates {{ animals }}',
  '{{ name }} loves {{ foods }}',
  '{{ name }} hates {{ foods }}',
  'My {{ animal }} loves {{ foods }}',
  'My {{ animal }} hates {{ foods }}',

  '{{ name }} is eating {{ a_food }}',
  '{{ name }}s {{ animal }} is eating {{ a_food }}',
  'The {{ animal }} is eating {{ name }}',
  'The {{ animal }} is eating {{ a_food }}',

  'Do {{ animals }} like {{ foods }}?',

  '{{ name }} is running away from {{ an_adjective }} {{ animal }}',

  '{{ uint }} {{ foods }}',
  '{{ uint }} {{ animals }}',
  'I have {{ uint }} {{ animals }}',
  'My {{ animal }} ate {{ uint }} {{ foods }}'
];
