import React from 'react';
import { TokenizeOutput } from '~/lib/TokenizeOutput';

import './VisualValidator.scss';

interface VisualValidatorProps {
  output: string,
  correctOutput: string
};

function VisualValidator(props: VisualValidatorProps) {
  
  const tokens = TokenizeOutput(props.output, props.correctOutput);
  const lineCount = tokens.length;

  return <div className="VisualValidator">{ tokens.map((line: any, lineIndex: number) => {
    return <React.Fragment key={lineIndex}>
      { 
        line.map((token: any, tokenIndex: number) => {
          if(token[0] < 0) {
            return <span key={tokenIndex} className="--incorrect">{ token[1] }</span>
          }
          else if(token[0] > 0) {
            return <span key={tokenIndex} className="--more"></span>
          }
          else {
            return <span key={tokenIndex} className="--correct">{ token[1] }</span>
          }
        })
      }
      { lineIndex < lineCount ? <br /> : '' }
    </React.Fragment>
  })}</div>
}

export default VisualValidator;