import { Token } from "./Token";

export const Patterns = {
  string: /\"(.*?)(?<!\\)\"|\".*|\'(.*?)(?<!\\)\'|\'.*/,
  number: /(?<!\w)\-?\.?\d+\.?\d*/,
  identifier: /[a-zA-Z_]\w*/,
  whitespace: / +/
};

export type GrammarRuleEvaluateFunction = (content: string) => Token|null

export interface GrammarRule {
  pattern: RegExp,
  type: string|number,
  evaluate?: GrammarRuleEvaluateFunction,
  __exactPattern?: RegExp
}

export const GrammarRules = {
  string: { 
    pattern: Patterns.string, 
    type: 'String',
    evaluate: (content: string) => {
      const firstChar = content.charAt(0);
      const lastChar = content.charAt(content.length-1);
      const closed = lastChar === firstChar && content.charAt(content.length-2) != '\\';
      return { type: 'String', content, closed }
    }
  },
  
  number: { 
    pattern: Patterns.number, 
    type: 'Number', 
    evaluate: (content: string) => {
      return {
        type: 'Number',
        content: (content.indexOf('.') < 0 ? parseInt(content) : parseFloat(content)) 
      }
    }
  },

  noWhitespace: { type: -1, pattern: Patterns.whitespace, evaluate: (content: string) => null }
}