import { VariableDictionary } from "./VariableDictionary";
import { randomInt } from "~/helpers/randomInt";

export class NumberVariableActions {
  public actions: any;
  public dictionary: VariableDictionary|null;

  private useVariablePercent = 0.5;
  private newVariablePercent = 0.5;

  public constructor(dictionary: VariableDictionary|null) {
    this.dictionary = dictionary;
    
    this.actions = {
      int10: () => {
        const obj = this._getRandomByKey('int10', randomInt(-10, 10));
        return obj.value;
      },
      int100: () => {
        const obj = this._getRandomByKey('int100', randomInt(-100, 100));
        return obj.value;
      },
      uint10: () => {
        const obj = this._getRandomByKey('uint10', randomInt(0, 10));
        return obj.value;
      },
      uint100: () => {
        const obj = this._getRandomByKey('uint100', randomInt(0, 100));
        return obj.value;
      },
      uint10_randomAsString: () => {
        const obj = this._getRandomByKey('uint10_randomAsString', randomInt(0, 10));
        return Math.random() < 0.5 ? "'" + obj.value + "'" : obj.value;
      },
      uint100_randomAsString: () => {
        const obj = this._getRandomByKey('uint100_randomAsString', randomInt(0, 100));
        return Math.random() < 0.5 ? "'" + obj.value + "'" : obj.value;
      },
      division_equation: () => {
        const n = randomInt(0, 10);
        const divNumber2 = randomInt(1, 10);
        const divNumber1 = n * divNumber2;
        
        let v1, v2;

        const divisionCount = this.dictionary?.getVariablesCountKeyStartsWith('division');

        if(Math.random() < this.useVariablePercent) {
          let variable = this.dictionary?.getRandomVariableByKey('division' + divisionCount);
          if(variable === null) {
            variable = this.dictionary?.create('division' + divisionCount, divNumber1);
          }
          v1 = variable?.name;
        } 
        else {
          v1 = divNumber1;
        }

        if(Math.random() < this.useVariablePercent) {
          let variable = this.dictionary?.getRandomVariableByKey('division' + divisionCount + 'r');
          if(variable === null) {
            variable = this.dictionary?.create('division' + divisionCount + 'r', divNumber2);
          }
          v2 = variable?.name;
        }
        else {
          v2 = divNumber2;
        }

        return v1 + ' / ' + v2;
      },
    }
  }

  private _getRandomByKey(key: string, newValue: any) {

    if(Math.random() < this.useVariablePercent) {
      return { value: newValue, isVariable: false };
    }

    let variable = this.dictionary?.getRandomVariableByKey(key);
    if(variable === null || Math.random() < this.newVariablePercent) {
      variable = this.dictionary?.create(key, newValue);
    }

    return { value: variable?.name, isVariable: true };
  }
}