import React from 'react';

import './ToggleSwitch.scss';

interface ToggleSwitchOnClickEvent {
  index: number,
  text: string
}

interface ToggleSwitchProps {
  options: Array<string>,
  optionWidth?: string,
  selectedIndex: number,
  label?: string,
  disabled?: boolean,
  onSelect?: (e: ToggleSwitchOnClickEvent) => void
}

function ToggleSwitch(props: ToggleSwitchProps) {

  const onClick = (index: number) => {
    if(props.disabled) {
      return;
    }
    if(index !== props.selectedIndex) {
      if(props.onSelect) {
        props.onSelect({ index, text: props.options[index] })
      }
    }
  }

  return <div className="ToggleSwitch">
    { props.label ? <h3>{ props.label }</h3> : '' }
    <div>{ props.options.map((option: string, index: number) => {
      return <a 
        key={index} 
        href="#" 
        className={ index === props.selectedIndex ? '--selected' : '' }
        onClick={e => {e.preventDefault(); onClick(index) }}
        style={ props.optionWidth ? { width: props.optionWidth } : {} }
      >{ option }</a>;
    })}</div>
  </div>
}

export default ToggleSwitch;