export type HighscoreKey = 'easy' | 'medium' | 'hard';

export function getHighscore(key: HighscoreKey): number {
  const data = localStorage.getItem(key);
  const highscore = data ? parseInt(data) : 0;
  return !isNaN(highscore) ? highscore : 0;
}

export function setHighscore(value: number, key: HighscoreKey) {
  localStorage.setItem(key, value.toString());
}