import { generateSentence } from "~/lib/SentenceGenerator/SentenceGenerator";
import { generateConsoleLog } from "../snippets";

export function consoleLog01() {
  let code = '';

  const count = 1 + Math.floor(Math.random() * 3); 
  for(let i = 0; i < count; i++) {
    code += generateConsoleLog(0, "'" + generateSentence() + "'") + ";\n";
  }

  return code;
}