import { consoleLog01 } from "./easy/consoleLog01";
import { consoleLog02 } from "./easy/consoleLog02";
import { consoleLog03 } from "./easy/consoleLog03";

const easyCodeFunctions: any = [
  [5,     consoleLog01],
  [10,    consoleLog02],
  [5,     consoleLog03],
];

export function generateEasyCode(): string {
  let maxValue = 0;
  for(let i = 0; i < easyCodeFunctions.length; i++) {
    maxValue += easyCodeFunctions[i][0];
  }

  const r = maxValue * Math.random();
  let v = 0;
  for(let i = 0; i < easyCodeFunctions.length; i++) {
    v += easyCodeFunctions[i][0];
    if(r < v) {
      return easyCodeFunctions[i][1]();
    }
  }

  return '';
}