export function getRandomAnimal(): string {
  return animals[Math.floor(animals.length * Math.random())];
}

export function getRandomAnimalPlural(): string {
  const animal = getRandomAnimal();
  const animalMinusOne = animal.substr(0, animal.length-1);
  const lastLetter = animal.charAt(animal.length-1);

  if(lastLetter === 'x') {
    return animal + 'es';
  }
  else {
    return animal + 's';
  }
}

