import { getRandomName } from './names';
import { getRandomAnimal, getRandomAnimalPlural } from "./animals";
import { getRandomAdjective } from './adjectives';
import { getRandomTemplate } from './templates';
import { getRandomFood, getRandomFoodPlural } from './foods';
import { randomInt } from '~/helpers/randomInt';

const actions:any = {
  name: () => { return getRandomName() },
  
  animal: () => { return getRandomAnimal() },
  animals: () => { return getRandomAnimalPlural() },
  an_animal: () => { return aPrefix(getRandomAnimal()) },

  adjective: () => { return getRandomAdjective() },
  an_adjective: () => { return aPrefix(getRandomAdjective()) },

  food: () => { return getRandomFood() },
  foods: () => { return getRandomFoodPlural() },
  a_food: () => { return aPrefix(getRandomFood()) },

  uint: () => { return randomInt(1, 100) }
};

export function generateSentence(config?: { templates?: string[], firstCharacterToUpperCase?: boolean, customActions?: any }): string {
  const template = config?.templates ? config.templates[Math.floor(config.templates.length * Math.random())] : getRandomTemplate();

  let sentence = '', token = '', nextToken = '';
  let action = '', isBuildingAction = false;
  
  for(let i = 0; i < template.length; i++) {
    token = template.charAt(i);
    nextToken = template.charAt(i+1);
    
    // Start of action.
    if(token + nextToken === '{{') {
      isBuildingAction = true;
      action = '';
      i++;
    }
    // End of action.
    else if(token + nextToken === '}}') {
      isBuildingAction = false;
      action = action.trim();
      i++;
      if(config && config.customActions && config.customActions[action]) {
        sentence += config.customActions[action]();
      }
      else if(actions[action]) {
        sentence += actions[action]();
      } else {
        console.warn('SentenceGenerator: The given action was not found, ' + action + '.');
      }
    }
    // Normal letter.
    else {
      if(isBuildingAction) {
        action += token;
      }
      else {
        sentence += token;
      }
    }
  }

  return config?.firstCharacterToUpperCase ? sentence.charAt(0).toUpperCase() + sentence.substr(1, sentence.length) : sentence;
}

const vowels = ['a', 'e', 'i', 'o', 'u'];
export function aPrefix(value: string, returnOnlyPrefix: boolean = false): string {
  const prefix = (vowels.indexOf(value.charAt(0).toLowerCase()) < 0 ? 'a ' : 'an ');
  return returnOnlyPrefix ? prefix : prefix + value;
}
