import { generateConsoleLog, generateEasyEquation } from "../snippets";
import { VariableDictionary, Variable } from "../VariableDictionary";

export function consoleLog03() {
  let code = '';

  const dictionary = new  VariableDictionary();

  let log = '';
  const count = Math.random() < 0.75 ? 1 : 1 + Math.floor(Math.random() * 3); 
  for(let i = 0; i < count; i++) {
    log += generateConsoleLog(0, generateEasyEquation(dictionary) ) + ";\n";
  }
  
  const declaredVariables: Array<Variable> = [];
  while(dictionary.variablesCount > 0) {
    const index = Math.floor(dictionary.variablesCount * Math.random());
    const variable = dictionary.removeVariableAt(index);

    if(Math.random() < 1.25 && declaredVariables.length > 0) {
      const variable2 = declaredVariables[Math.floor(declaredVariables.length * Math.random())];
      if(variable.key === variable2.key) {
        variable.value = variable2.name;
      }
    }

    declaredVariables.push(variable);
    code += 'let ' + variable.name + ' = ' + variable.value + ";\n";
  }

  code += log;

  return code;
}